<template>
  <div class="tk-px-padding-px20">
    <form
      class="navbar-search tk-layout-inline-block"
      style="width: calc(100% - 36px - 1rem); margin-right: 1rem"
    >
      <div class="form-group tk-px-margin-bottom-px0">
        <div
          class="input-group input-group-alternative input-group-merge tk-border-color-gray-200"
          style="box-shadow: none; height: 38px; background-color: #eff7f5"
        >
          <div class="input-group-prepend tk-layout-full-height">
            <span
              class="input-group-text"
              style="padding-top: 6px; padding-bottom: 4px"
              ><i class="fas fa-search"></i
            ></span>
          </div>
          <input
            class="form-control text-left tk-layout-full-height tk-font-color-gray ls-15"
            style="
              padding-top: 5px;
              padding-bottom: 5px;
              width: calc(100% - 54px);
            "
            placeholder="Search"
            type="text"
          />
        </div>
      </div>
    </form>
    <!-- <router-link :to="{ name: 'profile' }"> -->
    <!-- <div
      class="avatar avatar-sm rounded-circle tk-bg-color-card8 tk-layout-inline-block tk-cursor-pointer"
      @click.prevent="showProfile"
    >
      <img
        src="https://demos.creative-tim.com/vue-argon-dashboard-pro//img/theme/team-4.jpg"
      />
    </div> -->
    <div
      class="tk-layout-inline-block" 
      @click.prevent="showProfile">
      <avatar
      :uId="userId"
      :studioId="studioId"
      :name="name"
      sizeClass="avatar-sm"
      textClass="text-sm"
    ></avatar>
    </div>
    <!-- </router-link> -->
  </div>
</template>

<script>
import bus from "@/bus.js"
import collections from "@/services/collections"
import query from "@/services/query"
export default {
  components: {
    avatar: () => import("@/tkViews/components/layout/Avatar")
  },
  data () {
    return {
      userId: '',
      studioId: '',
      name: '',
    }
  },
  async created() {
    let self = this
    let user = localStorage.getItem("auth_user")
    if (!self.$tools.isNull(user)) {
      let data = JSON.parse(user)
      let userInfo = await query.getUserInfoByUserId(data.uid)
      let teacherInfo = await query.getTeacherInfoById(data.uid)
      self.userId = userInfo.userId
      self.studioId = teacherInfo.studioId
      self.name = userInfo.name
    }
  },
  methods: {
    showProfile() {
      bus.$emit("showProfile")
    }
  }
};
</script>

